.loader-wrapper {
  position: fixed;
  z-index: 999999;
  background: #fff;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.loader-wrapper .theme-loader {
  height: 100px;
  width: 100px;
  position: relative;
}
.loader-wrapper .theme-loader .loader-p {
  border: 0 solid transparent;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  position: absolute;
  top: calc(50vh - 75px);
  left: calc(50vw - 75px);
}
.loader-wrapper .theme-loader .loader-p:before {
  content: "";
  border: 1em solid var(--theme-default);
  border-radius: 50%;
  width: inherit;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
  animation: loader 2s linear infinite;
  opacity: 0;
  animation-delay: 0.5s;
}
.loader-wrapper .theme-loader .loader-p:after {
  content: "";
  border: 1em solid var(--theme-default);
  border-radius: 50%;
  width: inherit;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
  animation: loader 2s linear infinite;
  opacity: 0;
}
@keyframes loader {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

