/**=====================
  5.9 Box-layout CSS start
==========================**/
.box-layout {
  background-image: url(../images/other-images/boxbg.jpg);
  background-blend-mode: overlay;
  background-color: rgba($white, 0.5);
  .left-header {
    .left-menu-header{
      ul{
        &.header-left{
          li{ 
            &:last-child{
              display: none !important;
            }
          }
        }
      } 
    }
  } 
  .sale-card{
    .sale-date{
      a{
        span{
          display: none;
        }
      }
    }
  }
  .filter-cards-view{
    span{
      font-size: 14px;
    }
  } 
  .alert-primary{
    h4{
      color: var(--body-font-color); 
    }
    p{
      color: var(--body-font-color);
    }
  }
  .md-sidebar {
    .md-sidebar-toggle {
      display: block;
    }
    .md-sidebar-aside {
      position: absolute;
      top: 40px;
      left: 0;
      opacity: 0;
      visibility: hidden;
      z-index: 3;
      width: 280px;
      [dir="rtl"] & {
        left: unset;
        right: 0;
      }
      &.open { 
        opacity: 1; 
        visibility: visible;
      }
    }
    .job-sidebar {
      background-color: $body-color;
      padding: 20px;
      .faq-accordion.default-according {
        margin-bottom: 0;
        .row {
          >div {
            &:last-child {
              .card {
                margin-bottom: 0;
              }
            }
          }
        }
      }
    }
  }
  .ecommerce-dashboard{
    .last-orders-table {
      tbody {
        tr {
          td {
            .user-data {
              span {
                text-overflow:ellipsis;
                overflow:hidden; 
                display: -webkit-box !important;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical; 
                white-space: normal;
              } 
            }
          }
        }
      }
    }
    .sale-card {
      .sale-date {
        a{ 
          display: none;
        }
      }
    }
    .sale-data {
      ul {
        li {
          .sale-value {
            .sales-value{
              span{
                &:last-child{
                  text-overflow:ellipsis;
                  overflow:hidden;
                  display: -webkit-box !important;
                  -webkit-line-clamp: 1;
                  -webkit-box-orient: vertical; 
                  white-space: normal;
                }
              }
            }
          }
        }
      }
    }
  }
  .project-summary{
    thead{ 
      tr{
        th{
          &:nth-child(5){
            display: none;
          }
          &:nth-child(4){
            display: none;
          }
        } 
      }
    }
    tbody{
      tr{
        td{
          &:nth-child(5){
            display: none;
          }
          &:nth-child(4){
            display: none;
          }
        }
        &:nth-child(4){
          border-bottom: none;
          td{
            border-bottom: none;
            padding-bottom: 0px;
          }
        }
        &:last-child{
          border-bottom: none;
          td{
            display: none;
            border-bottom: none;
          }
        }
      }
    }
  } 
  .summary-card {
    .summary-progressbar {
      ul {
        li {
          width: 50%;
          &:nth-child(3){ 
            border-right: none;
          }
          &:last-child {
            display: none;
          }
        }
      }
    }
  }
  .last-orders-table{
    thead{
      tr{
        th{
          &:nth-child(3){ 
            display: none;
          }
          &:nth-child(5){
            display: none;
          }
        }
      }
    }
    tbody{
      tr{
        td{
          &:nth-child(3){ 
            display: none;
          }
          &:nth-child(5){
            display: none;
          }
        }
      }
    }
  }
  @media screen and (max-width: 1399px) and (min-width:1199px) {
    .proorders-xl-1 {
      order: 1; 
    }
   
    .proorders-xl-2 {
      order: 2;
    }
  
    .proorders-xl-3 {
      order: 3;
    }
  
    .proorders-xl-4 { 
      order: 4;
    }
  
    .proorders-xl-5 {
      order: 5;
    }
  
    .proorders-xl-6 {
      order: 6;
    }
  
    .proorders-xl-7 {
      order: 7;
    }
  
    .proorders-xl-8 {
      order: 8;
    }
  
    .proorders-xl-9 {
      order: 9;
    }
   
    .proorders-xl-10 {
      order: 10;
    }
  
  
    .proorders-xl-11 {
      order: 11;
    }
   
    .proorders-xl-12 {
      order: 12;
    }
  
    .proorders-xl-13 {
      order: 13;
    }
  }
