//progress color

$colors: (
  primary: $primary-color,
  secondary: $secondary-color,
  warning: $warning-color,
  info: $info-color,
  success: $success-color,
  danger: $danger-color,
  light: $light-color, 
  dark: $dark-color
); 

@each $key,
$val in $colors {
  .progress-striped-#{$key} {
    background-color: rgba($val, 0.1);

    .progress-bar {
      background-image: linear-gradient(120deg, rgba($val, 1) 25%, transparent 25%, transparent 50%, rgba($val, 1) 50%, rgba($val, 1) 75%, transparent 75%, transparent);
      background-size: 16px 30px;
      --bs-progress-bar-bg: rgba($val, 0.1);
      animation: progress-bar-stripes 5s linear infinite;
    }
  }

  .progress-border-#{$key} {
    &.progress-bar {
      position: relative;

      &:after {
        position: absolute;
        content: "";
        top: -2px;
        right: -9px;
        background-color: $val;
        width: 12px;
        height: 12px;
        border-radius: 100%;
      }
    }
  }

  .progress-border-#{$key} {
    &.progress {
      background-color: rgba($val, 0.15);
    }
    .progress-bar {
      position: relative;
      overflow: visible;
      background-color: $val; 
      border-radius: 15px 0 0 15px;

      [dir="rtl"] & { 
        border-radius: 0 15px 15px 0; 
      }

      &:after {
        position: absolute; 
        content: "";
        top: -4px;
        right: 0;
        background-color: $val;
        width: 1px;
        height: 16px;
        
        [dir="rtl"] & {
          left: 0px;
          right: unset;
        }
      }
    }

  }
}

@keyframes progress-bar-stripes {
  0% {
    background-position: 40px 0;
  }

  100% {
    background-position: 0 0;
  }
}

